import React from 'react';
import { Card, Avatar, Row, Col, Skeleton, Button, Modal, Input, message, } from 'antd';
import {
    UserOutlined,
    SearchOutlined,
    CloseCircleOutlined,
    SaveOutlined,
    CaretRightOutlined
} from '@ant-design/icons';
import axios from 'axios';
import { connect } from 'react-redux';

import Flex from 'components/shared-components/Flex';
import CreateClientForm from 'components/clients/CreateClientForm';

const StatusModal = (props) => {
    let { show, setShow, type, updateClientStatus, reasonValue, setReasonValue
    } = props;
    return (<Modal title="Alert"
        visible={show}
        onOk={updateClientStatus
        }
        centered onCancel={() => setShow(false)}>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
            <h2 style={{ margin: '0 auto' }}>
                {type} Reason
                </h2>
        </div>
        <Input value={reasonValue}
            onChange={e => setReasonValue(e.target.value)}
        />
    </Modal>
    )
}

const CreateClientModal = ({
    showForm,
    setShowForm,
    data,
    setData
}) => {
    return (<Modal title="Create Client"
        visible={showForm}
        centered footer={null}
        onCancel={() => setShowForm(false)}
        width={1000}
        destroyOnClose
    >
        <CreateClientForm
            setCreate={setShowForm}
            data={data}
            setData={setData}
        />
    </Modal>
    )
}

const SearchClient = (props) => {
    let { user_id, history } = props;
    let [loading, setLoading] = React.useState(false);
    let [data, setData] = React.useState({});
    let [show, setShow] = React.useState(false);
    let [type, setType] = React.useState('');
    let [reasonValue, setReasonValue] = React.useState('');
    let [showForm, setShowForm] = React.useState(false);

    const updateClientStatus = async () => {
        try {
            let body = {
                user_id,
                type,
                rawclient_id: data.rawclient_id,
                comment: reasonValue,
                ...data
            }
            setShow(false);
            let res = await axios.post('/saveorcancilsearchclient', body);
            message.success(res.data.message);
            setData({});
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data) :
                message.error(error.message);
        }
    }
    const getRandomClient = async () => {
        try {
            setLoading(true);
            let res = await axios.post('/randomsearchclient', {
                user_id
            });
            setData(res.data.data);
            setLoading(false);
        } catch (error) {
            setLoading(false);
            console.log(error);
            error.response ?
                message.error(error.response.data) :
                message.error(error.message);
        }
    }

    const onNextPress = () => {
        if (Object.keys(data).length !== 0) {
            setShowForm(true)
        } else {
            message.error('Search First');
        }
    }


    return (
        <div style={styles.container}>
            <StatusModal
                show={show}
                setShow={setShow}
                type={type}
                setReasonValue={setReasonValue}
                reasonValue={reasonValue}
                updateClientStatus={updateClientStatus}
            />
            <CreateClientModal
                showForm={showForm}
                setShowForm={setShowForm}
                data={data}
                setData={setData}
            />
            <div style={styles.firstColumn} >
                <Flex
                    alignItems='center'
                    justifyContent='center'
                    flexDirection='column'
                    style={styles.profileView}>
                    <Avatar size={90} icon={< UserOutlined />} />
                    <h1 style={{ marginTop: 20 }}>
                        {data.rawclient_contactperson || '--'}
                    </h1>
                    <Button
                        type="primary"
                        icon={< SearchOutlined />}
                        style={{ marginTop: 40, background: '#ffb619', border: '#ffb619' }}
                        onClick={getRandomClient}
                        disabled={data.rawclient_contactperson}>
                        Search
                         </Button>
                </Flex>
                <Flex
                    alignItems='center'
                    justifyContent='center'
                    flexDirection='row'
                    style={{ flex: 1 }}>
                    {
                        Object.keys(data).length !== 0 &&
                        <>
                            <Button
                                type="primary"
                                icon={< CloseCircleOutlined />}
                                className="mr-2"
                                danger
                                onClick={() => {
                                    setShow(true);
                                    setType('Cancel')
                                }}
                            >
                                Cancel
                            </Button>
                            <Button
                                type="primary"
                                icon={< SaveOutlined />}
                                onClick={() => {
                                    setShow(true)
                                    setType('Save')
                                }}
                            >
                                Save
                            </Button>
                        </>
                    }
                </Flex>
            </div>
            <div style={styles.secondColumn}>
                <h2 style={{ marginLeft: 20, }} >
                    Client Info
                </h2>
                {
                    loading ?
                        <Skeleton active={loading} />
                        :
                        <>
                            <Row justify='center' >
                                <Col md={11}
                                    style={styles.colView}>
                                    <span style={styles.title}>
                                        Client Name
                                        </span>
                                    { /* <span style={styles.value}>{data.rawclient_contactperson || '--'}</span> */}
                                    <input
                                        style={{ backgroundColor: '#f2f9ff', border: 0, ...styles.value }}
                                        onChange={e => setData({ ...data, rawclient_contactperson: e.target.value })}
                                        disabled={Object.keys(data).length === 0}
                                        value={data.rawclient_contactperson || '--'}
                                        className="input-focus"
                                    />
                                </Col>
                                <Col md={11}
                                    style={styles.colView} >
                                    <span style={styles.title}> Company Name </span>
                                    <input
                                        style={{ backgroundColor: '#f2f9ff', border: 0, ...styles.value }}
                                        onChange={e => setData({ ...data, rawclient_companyname: e.target.value })}
                                        disabled={Object.keys(data).length === 0}
                                        value={data.rawclient_companyname || '--'}
                                        className="input-focus"
                                    />
                                </Col>
                                <Col md={11} style={styles.colView} >
                                    <span style={styles.title}>Client Email</span>
                                    <input
                                        style={{ backgroundColor: '#f2f9ff', border: 0, ...styles.value }}
                                        onChange={e => setData({ ...data, rawclient_email: e.target.value })}
                                        disabled={Object.keys(data).length === 0}
                                        value={data.rawclient_email || '--'}
                                        className="input-focus"
                                    />
                                </Col>
                                <Col md={11} style={styles.colView}>
                                    <span style={styles.title}> Alternate Email </span>
                                    <input
                                        style={{ backgroundColor: '#f2f9ff', border: 0, ...styles.value }}
                                        onChange={e => setData({ ...data, rawclient_alternateemail: e.target.value })}
                                        disabled={Object.keys(data).length === 0}
                                        value={data.rawclient_alternateemail || '--'}
                                        className="input-focus"
                                    />
                                </Col>
                                <Col md={11} style={styles.colView}>
                                    <span style={styles.title}> Phone Number </span>
                                    <input
                                        style={{ backgroundColor: '#f2f9ff', border: 0, ...styles.value }}
                                        onChange={e => setData({ ...data, rawclient_officenumber: e.target.value })}
                                        disabled={Object.keys(data).length === 0}
                                        value={data.rawclient_officenumber || '--'}
                                        className="input-focus"
                                    />
                                </Col>
                                <Col md={11} style={styles.colView}>
                                    <span style={styles.title}> Alternate Phone number </span>
                                    <input
                                        style={{ backgroundColor: '#f2f9ff', border: 0, ...styles.value }}
                                        onChange={e => setData({ ...data, rawclient_alternateofficenumber: e.target.value })}
                                        disabled={Object.keys(data).length === 0}
                                        value={data.rawclient_alternateofficenumber || '--'}
                                        className="input-focus"
                                    />
                                </Col>
                                <Col md={11} style={styles.colView}>
                                    <span style={styles.title}> State </span>
                                    <input
                                        style={{ backgroundColor: '#f2f9ff', border: 0, ...styles.value }}
                                        onChange={e => setData({ ...data, rawclient_state: e.target.value })}
                                        disabled={Object.keys(data).length === 0}
                                        value={data.rawclient_state || '--'}
                                        className="input-focus"
                                    />
                                </Col>
                                <Col md={11} style={styles.colView} >
                                    <span style={styles.title}> City </span>
                                    <input
                                        style={{ backgroundColor: '#f2f9ff', border: 0, ...styles.value }}
                                        onChange={e => setData({ ...data, rawclient_city: e.target.value })}
                                        disabled={Object.keys(data).length === 0}
                                        value={data.rawclient_city || '--'}
                                        className="input-focus"
                                    />
                                </Col>
                            </Row>
                            <Button
                                type="primary"
                                style={{ marginRight: 40, marginTop: 30, float: 'right' }}
                                icon={< CaretRightOutlined />}
                                onClick={onNextPress} >
                                Next
                            </Button>
                        </>
                }
            </div>
        </div>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id
});

export default connect(mapStateToProps)(SearchClient);

const styles = {
    container: {
        display: 'flex',
        flex: 1,
        flexDirection: 'row',
        height: '100%',
    },
    firstColumn: {
        display: 'flex',
        flexDirection: 'column',
        flex: 1,
        background: 'white',
        border: '1px solid #edf2f9',
        borderRadius: 5
    },
    secondColumn: {
        flex: 3,
        background: 'white',
        border: '1px solid #edf2f9',
        borderRadius: 5,
        marginLeft: 20,
        paddingTop: 20
    },
    profileView: {
        flex: 1,
    },
    colView: {
        backgroundColor: '#f2f9ff',
        marginRight: 20,
        marginTop: 20,
        padding: '10px 20px',
        borderRadius: 5,
        borderLeft: "3px solid rgb(92, 180, 255)",
    },
    title: {
        // marginLeft: 20,
        color: 'gray',
        display: 'block',
    },
    value: {
        display: 'block',
        fontSize: 20
    }
}